    transition: transform .3s
                cubic-bezier(0, .52, 0, 1), -webkit-transform .3s
                cubic-bezier(0, .52, 0, 1);
    overflow: scroll;
    z-index: 500;
  }
  #flyoutMenu {
    width: 80vw;
    height: 100vh;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    transition: -webkit-transform .3s
                cubic-bezier(0, .52, 0, 1);
    transition: transform .3s
                cubic-bezier(0, .52, 0, 1);
    transition: transform .3s
                cubic-bezier(0, .52, 0, 1), -webkit-transform .3s
                cubic-bezier(0, .52, 0, 1);
    overflow: scroll;
    z-index: 1000;
  }
  
  #flyoutMenu.hide {
    -webkit-transform: translate3d(-82.63vw, 0, 0);
            transform: translate3d(-82.63vw, 0, 0);
  }
  
  #flyoutMenu.show {
    -webkit-transform: translate3d(-2.63vw, 0, 0);
            transform: translate3d(-2.63vw, 0, 0);
    overflow: hidden;
  }
  
  #flyoutMenu h2 a {
